.nickName {
  font-family: "Courier New", Courier, monospace;
  width: 10vw;
  height: 1rem;
}
.container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
}
.item {
  flex-basis: 150px;
  flex-grow: 1;
  flex-shrink: 0;
  background-color: brown;
}
.itemStartTime {
  flex-basis: content;
  flex-grow: 1;
  background-color: yellow;
}
.itemEndTime {
  flex-basis: content;
  flex-grow: 10;
  background-color: greenyellow;
}
